	module.exports = {"data":{"site":{"siteMetadata":{"title":"Elvin's Blog","author":"Elvin Ma"}},"markdownRemark":{"id":"/Users/maxingcong/Code/blog/src/pages/VS Code的几个使用tip.md absPath of file >>> MarkdownRemark","html":"<p><strong>从命令行打开当前目录</strong></p>\n<p>按 Shift + Command + P，调出Command Panel 输入并选择 Install ‘code’ command in PATH，如果没有这条命令的话，则有可能是已经添加过了，然后再打开terminal里执行code，就可以用VS Code打开当前目录了</p>\n<p><strong>自定义Snippets</strong></p>\n<p>查找 Perferences -> Snippets，然后按提示选择一种语言，并且按规则创建就可以了，输入对应的 prefix 时，就会出现对应用的snippet的提示title，选中回车后就可以插入你创建的模板代码段</p>\n<p><strong>快捷Build命令</strong></p>\n<p>在package.json里scripts添加一个build指令，然后按Command + Shift + B，就可以通过快捷键在编辑器里直接运行 build sc</p>\n<p><strong>全局符号替换</strong></p>\n<p>在JS项目中，选中函数名或者变量名，按 Commmand + Shift + P 或者 F2 输入，rename symbol，按提示输入替换字符串即可，在typescript项目中很有用</p>\n<p><strong>隐藏文件</strong></p>\n<p>按 Shift + Command + P，输入setting json，打开编辑器设置，比如想在typescript中项目中隐藏生成的js文件，可以在<code class=\"language-text\">&quot;files.exclude&quot;</code>选项里添加一行：<code class=\"language-text\">&quot;**/*.js&quot;: {&quot;when&quot;: &quot;${basename}.ts&quot;}</code></p>","frontmatter":{"title":"VS Code的几个使用tip","tags":null,"categories":["编程"],"date":"December 02, 2017"}}},"pathContext":{"slug":"/VS Code的几个使用tip/","previous":{"fields":{"slug":"/事件循环原理/"},"frontmatter":{"path":"/how-event-loop-works","title":"事件循环原理","tags":["javascript"],"categories":["编程"]}},"next":{"fields":{"slug":"/箭头函数总结/"},"frontmatter":{"path":"/arrow-function-summary","title":"箭头函数总结","tags":["Javascript"],"categories":["编程"]}}}}

